
import { defineComponent, ref } from "vue";

const Component = defineComponent({
  name: "EDMTemplateCard",
  props: {
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(_, { emit }) {
    const visible = ref(false);

    const onDuplicateClick = () => {
      visible.value = false;
      emit("duplicate");
    };
    const onDeleteClick = () => {
      visible.value = false;
      emit("delete");
    };

    return {
      visible,

      onDuplicateClick,
      onDeleteClick
    };
  }
});

export default Component;
