<template>
  <div id="edm-template-card-component">
    <div class="wrapper">
      <slot></slot>

      <el-popover
        v-if="editable"
        v-model:visible="visible"
        placement="bottom"
        :offset="2"
        :width="80"
        trigger="click"
        :show-arrow="false"
      >
        <div class="content">
          <p @click="onDuplicateClick">Duplicate</p>
          <p @click="onDeleteClick">Delete</p>
        </div>
        <template #reference>
          <div class="edit-icon">
            <font-awesome-icon
              class="icon"
              :icon="['fas', 'ellipsis-h']"
            ></font-awesome-icon>
          </div>
        </template>
      </el-popover>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

const Component = defineComponent({
  name: "EDMTemplateCard",
  props: {
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(_, { emit }) {
    const visible = ref(false);

    const onDuplicateClick = () => {
      visible.value = false;
      emit("duplicate");
    };
    const onDeleteClick = () => {
      visible.value = false;
      emit("delete");
    };

    return {
      visible,

      onDuplicateClick,
      onDeleteClick
    };
  }
});

export default Component;
</script>

