<template>
  <div id="edm-component">
    <div class="top-container">
      <el-button type="success">Create EDM</el-button>
      <div class="select-item">
        <label class="label">Team</label>
        <el-select v-model="teamValue" placeholder="Select">
          <el-option value="Nike">Nike</el-option>
          <el-option value="Brand Name">Brand Name</el-option>
        </el-select>
      </div>
    </div>
    <el-row :gutter="30">
      <el-col
        v-for="item in filteredList.sort((a, b) => b.id - a.id)"
        :key="item.id"
        :span="8"
      >
        <EDMTemplateCard
          editable
          @click="onCardClick(item.id)"
          @duplicate="onDuplicateClick(item)"
        >
          <img
            class="edm-cover-image"
            :src="getCover(item.chosen_template)"
            alt="cover"
          />
        </EDMTemplateCard>
        <p class="name">{{ item.subject }}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, ref } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import EDMTemplateCard from "@/components/EDMTemplateCard.vue";
import { COVER_URL, getCover } from "@/constants/edm-cover";
import { GetEdms, PostCreateEdm, PostDuplicateEdm } from "@/typings/store";
import { EdmCardResponse, EdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { loading } from "@/utils";

const Component = defineComponent({
  name: "EDM",
  components: {
    EDMTemplateCard
  },
  setup: function() {
    const router = useRouter();
    const store = useStore();
    const teamValue = ref("");

    const list = computed(() => store.state.edm.list);
    const filteredList = computed(() =>
      teamValue.value
        ? list.value.filter(
            (it: EdmCardResponse) => it.recipient_brand === teamValue.value
          )
        : list.value
    );

    const init = async () => {
      const loadingInstance = loading();
      await store.dispatch(GetEdms);
      loadingInstance.close();
    };

    onMounted(async () => await init());

    const onCardClick = async (id: number) => {
      let _id = id;
      if (_id < 0) {
        const params: EdmParams = {
          subject: `New EDM`,
          recipient_brand: "Brand Name",
          message: "Message",
          message_body: "Text",
          html_message: "<div></div>",
          creator: 1, // TODO 確認之後是否還要帶
          chosen_collection: null,
          chosen_template: Math.abs(_id)
        };
        const data = await store.dispatch(PostCreateEdm, params);
        _id = data.id;
        ElNotification({
          title: "Success",
          type: "success",
          message: `Create new EDM successful.`
        });
      }
      await router.push({ name: "EDMDisplay", params: { id: _id } });
    };

    const onDuplicateClick = async (item: EdmCardResponse) => {
      try {
        await store.dispatch(PostDuplicateEdm, item.id);
        ElNotification({
          title: "Success",
          type: "success",
          message: `EDM ${item.subject} duplicated successful.`
        });
        await init();
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
    };

    return {
      teamValue,
      filteredList,

      onCardClick,
      onDuplicateClick,
      getCover,

      COVER_URL
    };
  }
});

export default Component;
</script>

